import {Component, OnInit, ViewChild} from '@angular/core';
import {NgForm} from '@angular/forms';
import {HttpClient} from '@angular/common/http';
import {timeout} from 'rxjs/operators';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  @ViewChild('f', {static: false}) regForm: NgForm;
  submitted = false;
  user = {
    username: '',
    email: '',
    password: ''
  };
  message = null;

  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  onSubmit() {
    if (this.regForm.value.password !== this.regForm.value.confirmPassword) {
      this.message = 'Password are not matching';
    } else {
      this.message = '';
      this.submitted = true;
      this.user.username = this.regForm.value.username;
      this.user.email = this.regForm.value.email;
      this.user.password = this.regForm.value.password;
      this.http.post('https://adm-api.herokuapp.com/register', this.user)
        .subscribe(responseData => {
          setTimeout(() => {
            this.message = responseData["message"];
          }, 2000);
          setTimeout(() => {
            this.message = '';
          }, 5000);
          console.log(responseData);
        });
    }
  }

}
